@import url(https://fonts.googleapis.com/css?family=Lato:200, 400);
@import url(https://fonts.googleapis.com/css?family=Lustria);
.Hyph {
    margin-right: 5px;
    margin-left: 5px;
  }
  
  .Input,
  .Textarea {
    border: 1px solid #ddd;
    display: block;
    font-size: 1.1em;
    margin-bottom: 10px;
    padding: 10px;
    text-align: left;
    width: 100%;
    border-radius: 6px;
  }
  
  .Input::-webkit-input-placeholder, .Textarea::-webkit-input-placeholder {
    color: #ccc;
  }
  
  .Input:-ms-input-placeholder, .Textarea:-ms-input-placeholder {
    color: #ccc;
  }
  
  .Input::-ms-input-placeholder, .Textarea::-ms-input-placeholder {
    color: #ccc;
  }
  
  .Input::placeholder,
  .Textarea::placeholder {
    color: #ccc;
  }
  
  .Textarea {
    resize: vertical;
  }
  
  .Button {
    order: 1px solid #aaa;
    background-color: cornsilk;
    border: 1px solid rgba(31, 31, 41, 0.824);
    color: cornflowerblue;
    font-size: 13px;
    border-radius: 6px;
    padding: 8px 12px;
    font-weight: bold;
  }

  .Button:hover {
    background-color: cornflowerblue;
    color: cornsilk;
    font-size: 14px;
  }
  
  .Required {
    color: #CA1551;
    font-family: Arial;
    font-weight: bold;
  }
  
  .Section {
    margin-left: 20px;
    margin-right: 20px;
  }
  .Section--list {
    margin-left: 10px;
    margin-right: 10px;
  }
  .Header {
    height: 40px;
    display: flex;
    align-items: normal;
    justify-content: space-between;
    margin-bottom: 60px;
  }
  
  .Header h1 {
    margin: 0;
  }
  
  .Header h1 a {
    text-decoration: none;
    font-weight: bold;
    -webkit-text-decoration: underline cornflowerblue;
            text-decoration: underline cornflowerblue;
    -webkit-text-decoration-style: wavy;
            text-decoration-style: wavy;
    text-shadow: 0 0 6px #FF0000, 0 0 10px cornflowerblue;
  }
  
  .Header a {
    color:cornsilk;
  }

  .Header h1 a:hover {
    color: cornflowerblue;
    font-size: 30px;
  }
  
  .Header__not-logged-in {
    display: flex;
    flex-direction: column;
    font-weight: bold;
    text-shadow: 0 0 10px black;
  }

  .Header__not-logged-in a:hover {
    color: cornflowerblue;
    font-size: 18px;
    text-shadow: 0 0 6px #FF0000, 0 0 10px cornflowerblue;
  }
  
  .Header__not-logged-in .Hyph {
    display: none;
  }

  .Header__logged-in {
    display: flex;
    flex-direction: column;
    font-weight: bold;
    text-shadow: 0 0 10px black;
  }

  .Header__logged-in a:hover {
    color: cornflowerblue;
    font-size: 18px;
    text-shadow: 0 0 6px #FF0000, 0 0 10px cornflowerblue;
  }
  
  @media screen and (min-width: 450px) {
    .Header {
      align-items: baseline;
    }
    .Header h1 {
      font-size: 22pt;
    }
  
    .Header__not-logged-in {
      flex-direction: row;
    }
  
    .Header__not-logged-in .Hyph {
      display: inline;
      display: initial;
    }
  }
.ListWishList {
    background: none;
    font-size: smaller;
    display: block;
    padding: 10px 10px 0;
    margin-bottom: -1px;
    border: 1px solid darkgray;
    border-radius: 6px;
    text-align: center;
    width: 100%;
    text-decoration: none;
    margin-top: 0;
  }

  .ListWishList__Container{
    display: flex;
    flex-direction: column;
  }

  .Button__DeleteList{
    background-color: cornsilk;
    border: 1px solid rgba(31, 31, 41, 0.824);
    color: cornflowerblue;
    font-size: 12px;
    border-radius: 6px;
    font-weight: bold;
    
  }

  .Button__DeleteList:hover {
    font-size: 14px;
    border: 1px solid rgba(31, 31, 41, 0.824);
  }

  .card{
    border-radius: 6px;
    background-color: rgba(238, 230, 255, 0.577);
    margin: 5px;
    border: 1px solid darkgrey;
  }
  
  .ListWishList:hover {
    cursor: pointer;
    background-color: #f7f8f9;
    position: relative;
    padding-top: 5px;
    font-size: 16px;
  }
  
  .ListWishList,
  .ListWishList:visited {
    color: #555;
  }
  
  .ListWishList__header {
    align-items: baseline;
    display: flex;
    justify-content: center;
  }

  .ListWishList__header h2{
    font-weight: bold;
    text-shadow: 2px 2px 3px #FF0000;
  }
  
  .ListWishList__heading {
    margin: 0;
  }

  .ListWishList__description {
      font-style: italic;
      color: black;
      font-size: 15px;
  }
  
.Button__CreateList{
    background-color: rgba(228, 228, 250, 0.831);
    border: 1px solid rgba(31, 31, 41, 0.824);
    border-radius: 6px;
    color: rgb(37, 35, 58);
    font-size: 13px;
    margin-top: 5px;
    font-weight: bold;
  }

.List_Title, .List_Description{
